import React from "react"
import { Link, graphql } from "gatsby"
import Img from "gatsby-image"
import styled from "@emotion/styled"
import { keyframes } from "@emotion/core"
import strip from "../images/strip.svg"
import { ReactComponent as Tag } from "../images/tag.svg"
import PropTypes from "prop-types"
import { snakeCase } from "lodash"
import { Header, HeaderIcon, ExpandableHeaderTitle, LinkWithIcon} from "../components/style"
import posed from "react-pose"
import { ReactComponent as AllIcon } from "../images/all.svg"
import { ReactComponent as TagIcon } from "../images/tag.svg"
import { ReactComponent as LineSep } from "../images/separator.svg"
import color from "../components/color"
// smoll
const FImage = styled(Img)`
  @media only screen and (min-width: 1400px) {
   width : 100%;
   display: block;
   margin-top: 0;
  }
  margin-top: 14px;
  width : 30%;
  display: inline-block;
`

const topToBottom = keyframes`
  0% {
    opacity: 0;
    transform: translateY(150px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
`

const PostContainer = styled.div`
  display: inline-block;
  opacity: 0;
  width: calc(100vw - 20px);
  margin: 10px;
  
  @media only screen and (min-width: 768px) {
    width: 350px;
    margin: 25px 10px;
  }
  
  @media only screen and (min-width: 1400px) {
   width: 30%;
   margin: 25px 10px;
  }

  &:nth-of-type(1) {
    animation: ${topToBottom} 1s ease-out 0s forwards;
  }
  &:nth-of-type(2) {
    animation: ${topToBottom} 1s ease-out 0.2s forwards;
  }
  &:nth-of-type(3) {
    animation: ${topToBottom} 1s ease-out 0.4s forwards;
  }
  &:nth-of-type(4) {
    animation: ${topToBottom} 1s ease-out 0.4s forwards;
  }
  &:nth-of-type(5) {
    animation: ${topToBottom} 1s ease-out 0.6s forwards;
  }
  &:nth-of-type(6) {
    animation: ${topToBottom} 1s ease-out 0.8s forwards;
  }
  &:nth-of-type(7) {
    animation: ${topToBottom} 1s ease-out 0.8s forwards;
  }
  &:nth-of-type(8) {
    animation: ${topToBottom} 1s ease-out 1s forwards;
  }
  &:nth-of-type(9) {
    animation: ${topToBottom} 1s ease-out 1.2s forwards;
  }
`

const PostContent = styled.div`
  display: inline-block;
  p {
    font-size: 1rem;
    text-overflow: ellipsis;
    margin: 3px 10px;
    overflow: hidden;
  }
  width: 70%;
  vertical-align: top;
  margin-top: 15px;
  @media only screen and (min-width: 1400px) {
   width: 100%;
   margin-top: 0;
  }
`

const Card = styled.div`
  text-align: left;
  min-width: 250px;
  transition: transform 0.2s ease-out;
  height: 100%;
  position: relative;

  &:hover{
    transform: translateY( -2px);
    transition: transform 0.3s ease-out;
  }
`

const CardHeader = styled.div`
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 25px;
`

const CHTop = styled.div`
  width: 100%;
  height: 15px;
  float: left;
  background-color: ${color.overlay};
`

const CHDiagonal = styled.div`
  float: right;
  position: relative;
  width: 5px;
  height: 5px;
  
   &:before {
    content: "";
    display: block;

    /* position the triangle in the top right corner */
    position: absolute;
    z-index: 0;
    top: 0;
    right: 0;

    /* create the triangle */
    width: 0;
    height: 0;
    border: 5px solid transparent;
    border-top-color:  ${color.overlay};
    border-right-color:  ${color.overlay};
  }
`

const CHContent = styled.div`
  float: right;
  background-color: ${color.overlay};
  height: 10px;
`

const CHContentText = styled.div`
  margin-top: -15px;
  padding: 0 10px;
  color: ${color.background_base};
  &:before {
    content: "#";
    color: ${color.pound_mark};
    font-weight: bold;
    margin-right: 2px;
  }
`

const PostHeader = styled.div`
  font-size: smaller;
  color: ${color.date_info};
  margin: 5px 10px;
`

const PostTitle = styled.h3`
  margin: 5px 10px;
`

const PostExcerpt = styled.p`
  height: 85px;
`

const Separator = styled.div`
  width:100%;
  height: 5px;
  background-color: ${color.image_separator};
  background-image: url(${strip});
  background-repeat: no-repeat;
  background-position: right 20px center;
  @media only screen and (min-width: 1400px) {
   display: block;
  }
  display: none;
`

const ContentTagSeparator = styled(LineSep)`

  border-left: 1px solid ${color.border};
  border-right: 1px solid ${color.border};
  width: calc(100% - 2px);
  display: block;

`
const Categories = styled(LinkWithIcon)`
    height: 25px;
    margin-left: 10px;
`
const BlogWrapper = styled.div`
  margin: 0 auto;
`

const PosedBlogWrapper = posed(BlogWrapper)({
  enter: {
    opacity: 1,
    delay: 350,
    transition: { ease: "easeOut", duration: 300 },
  },
  exit: {
    opacity: 0,
    transition: { ease: "easeOut", duration: 300 },
  },
})

const TagWrapper = styled.div`
  height: 30px;
   width: calc(100vw - 52px);
  display: inline-block;
  vertical-align: top;
  border-left: 1px solid ${color.border};
  border-bottom: 1px solid ${color.border};
  
  @media only screen and (min-width: 768px) {
    width: 318px;
  }
  @media only screen and (min-width: 1400px) {
    width: 268px;
  }
`

const DiagonalCut = styled.div`
  height: 30px;
  width: 33px;
  padding: 0;
  display: inline-block;
  position: relative;
  transform: skewX(-45deg);
  margin: 0 0 0 -18px;
  border-right: 1px solid ${color.border};
`

const PostList = styled.div`
  display: inline-block;
  width: 100vw;

  @media only screen and (min-width: 768px) {
    width: 745px;
  }
  
  @media only screen and (min-width: 1400px) {
    width: 1000px;
  }
`
const Container = styled.div`
  justify-content: space-between;
  flex-wrap: wrap;
  display: flex;
`

const TagText = styled.span`
  vertical-align: middle;
`

const CardLink = styled(Link)`
  display: block;
  border: 1px solid ${color.border};
  border-bottom: 0;
  height: calc(100% - 35px);
`

class BlogIndex extends React.Component {
  static propTypes = {
    location: PropTypes.object.isRequired,
  }

  state = {
    isExpanded: false,
    isSearchMode: false
  };

  expandSearch = () =>{
    this.setState(state => ({ isSearchMode: !state.isSearchMode }));
  };


  preventClick = (e,currentPath) => {
    if(currentPath===1) {
      e.preventDefault();
      e.stopPropagation();
      e.nativeEvent.stopImmediatePropagation();
    }
  };

  render() {
    const { data, pageContext } = this.props
    const { category } = pageContext
    return (
      <>
      <PosedBlogWrapper>
        <Header>
          <HeaderIcon>
            {category === undefined ? <AllIcon/>: <TagIcon/>}
          </HeaderIcon>
          <ExpandableHeaderTitle>
              <h1>{category === undefined ? 'All Posts': category}</h1>
              {data.allMarkdownRemark.totalCount} Posts
          </ExpandableHeaderTitle>
        </Header>
        <PostList>
          <Container>
            {data.allMarkdownRemark.edges.map(({ node }) => (
              <PostContainer key={node.id}>
                <Card>
                  {/* immediately-invoked function expressions to wrap the if */}
                  <CardLink to={node.fields.path}>
                    {(() => {
                      if (node.frontmatter.featuredImage) {
                        return <FImage fluid={node.frontmatter.featuredImage.childImageSharp.fluid}/>
                      } else {
                        return <FImage fluid={data.mainBg.childImageSharp.fluid}/>
                      }
                    })()}
                    <CardHeader>
                      <CHTop/>
                      <CHContent>
                        {(() => {
                          if (node.frontmatter.postNumber) {
                            return <CHContentText>{node.frontmatter.postNumber}</CHContentText>
                          }
                        })()}
                      </CHContent>
                      <CHDiagonal/>
                    </CardHeader>
                    <Separator/>
                    <PostContent>
                      <PostHeader>{node.frontmatter.date} </PostHeader>
                      <PostTitle>
                        {node.frontmatter.title}
                      </PostTitle>
                      <PostExcerpt>{node.excerpt}</PostExcerpt>
                    </PostContent>
                  </CardLink>
                  <ContentTagSeparator/><TagWrapper>
                    {node.frontmatter.categories.map((tag, i) => {
                      return (
                        <Categories key={"tag-" + tag + "-" + i} to={`/category/${snakeCase(tag)}/`}>
                          <div><Tag/><TagText>{tag}</TagText>
                          </div>
                        </Categories>
                      )
                    })}
                  </TagWrapper>
                  <DiagonalCut/>
                </Card>
              </PostContainer>
            ))}
          </Container>
        </PostList>
      </PosedBlogWrapper>
        </>
    )
  }
}

export default BlogIndex

export const query = graphql`
  query blogListQuery($filter: MarkdownRemarkFilterInput, $skip: Int!, $limit: Int!) {
    site {
      siteMetadata {
        title
      }
    }
     allMarkdownRemark(
      sort: { fields: [frontmatter___date], order: DESC }
      filter: $filter
      limit: $limit
      skip: $skip
    )  {
      totalCount
      edges {
        node {
          id
          frontmatter {
            title
            date(formatString: "DD MMMM YYYY")
            author
            featuredImage {
              childImageSharp {
                fluid(maxWidth: 300) {
                  ...GatsbyImageSharpFluid
                }
              }
            }
            categories
            postNumber
          }
          fields {
            slug
            path
          }
          excerpt(pruneLength: 120)
        }
      }
    }
     mainBg: file(relativePath: { eq: "placeholder.png" }) {
      childImageSharp {
        fluid(maxWidth: 800) {
          ...GatsbyImageSharpFluid
        }
      }
    }
  }
`